.container {
  width: calc(100 * var(--vw, 1vw));
  max-width: 1366px;
  margin: 0 auto;
  padding: 0 103px;

  @include vp-1199 {
    padding: 0 45px;
  }

  @include vp-767 {
    padding: 0 15px;
  }
}

.container--fullscreen-tablet {
  @include vp-1199 {
    padding: 0;
  }

  @include vp-767 {
    padding: 0;
  }
}

.container--fullscreen-mobile {
  @include vp-767 {
    padding: 0;
  }
}
